<template>
  <div class="home contain-footer body-c">
    <!--设备列表-->
    <dev-list>
      <cell-swipe
        class="cell-swipe"
        v-for="(item,index) in devItem"
        :key="index"
        :right="[
          {
            content: '删除',
            style: { background: '#ff3b30', color: '#fff' },
            handler:function() {

            }
          },
          {
            content: '关闭',
            style: { background: '#c7c7cc', color: '#fff' }
          }
        ]">
        <dev-item
          :signalActive="item.signalActive"
          :title="item.title+item.infoItem.length"
          settingPath="/setting"
          :infoItem="item.infoItem"
          :flexOne="item.infoItem.length<=1?true:false">
        </dev-item>
      </cell-swipe>
    </dev-list>
    <!--footer-->
    <footer-page>
      <footer-item
        @click.native="addDevice"
        icon="icon-jia"
        content="添加设备">
      </footer-item>
    </footer-page>
  </div>
</template>
<script>
  import {DevList, DevItem} from '../components/dev-list'
  import {Footer, FooterItem} from '../components/footer'
  import {CellSwipe, MessageBox} from 'mint-ui'

  export default {
    name: 'Home',
    data () {
      return {
        devItem: [
          {
            title: '我的开关01',
            infoItem: [
              {path: '/dev-switch', infoTitle: '通道01', infoState: '开'}
            ]
          },
          {
            title: '我的开关02',
            infoItem: [
              {path: '/dev-switch', infoTitle: '通道01', infoState: '开'},
              {path: '/dev-switch', infoTitle: '通道02', infoState: '开'},
              {path: '/dev-switch', infoTitle: '通道03', infoState: '开'},
              {path: '/dev-switch', infoTitle: '通道04', infoState: '关'}
            ]
          },
          {
            title: '我的开关03',
            signalActive: true,
            infoItem: [
              {path: '/dev-switch', infoTitle: '通道01', infoState: '开'},
              {path: '/dev-switch', infoTitle: '通道02', infoState: '开'},
              {path: '/dev-switch', infoTitle: '通道03', infoState: '开'},
              {path: '/dev-switch', infoTitle: '通道04', infoState: '关'}
            ]
          }
        ]
      }
    },
    components: {
      'cell-swipe': CellSwipe,
      'dev-list': DevList,
      'dev-item': DevItem,
      'footer-page': Footer,
      'footer-item': FooterItem
    },
    methods: {
      //删除设备
      delDevice: function () {
        MessageBox({
          title: '提示',
          message: '确定执行此操作?',
          showCancelButton: true
        })
      },
      //添加设备
      addDevice: function () {
        this.$router.push('/enter-password')
      }
    }
  }
</script>
<style lang="less">
  .home {
    .cell-swipe{
      background: none !important;
      .mint-cell-wrapper{
        padding: 0 !important;
      }
    }
  }
  .mint-cell-value{
    width: 100%;
  }
</style>



